import React, { useContext, useEffect, useState } from "react";
import { Formik, Form, Field } from "formik";
import * as Yup from "yup";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "src/helper/AuthContext";

import * as S from "./styled";

export const TimeInput = () => {
  const { authState } = useContext(AuthContext);
  const navigate = useNavigate();
  const initialValues = {
    kor: "",
    eng: "",
    math: "",
    sci: "",
    com: "",
    kh: "",
    study: "",
  };
  useEffect(() => {
    if (!localStorage.getItem("accessToken")) {
      navigate("/auth/login");
      alert("   .");
    }
  }, []);
  const validationSchema = Yup.object().shape({
    kor: Yup.string().required(""),
    eng: Yup.string().required(""),
    math: Yup.string().required(""),
    sci: Yup.string().required(""),
    com: Yup.string().required(""),
    kh: Yup.string().required(""),
    study: Yup.string().required(""),
  });

  const onSubmit = (data) => {
    axios
      .post("http://localhost:8080/todaytimes", data, {
        headers: { accessToken: localStorage.getItem("accessToken") },
      })
      .then((response) => {
        alert("Success");
      });
  };
  return (
    <>
      <S.InputTimeContainer>
        <S.InputContainer>
          <Formik
            initialValues={initialValues}
            onSubmit={onSubmit}
            validationSchema={validationSchema}
          >
            <Form style={{ display: "flex", flexDirection: "column" }}>
              <label></label>
              <Field autoComplete="off" name="kor" />
              <label></label>
              <Field autoComplete="off" name="eng" />
              <label></label>
              <Field autoComplete="off" name="math" />
              <label></label>
              <Field autoComplete="off" name="sci" />
              <label></label>
              <Field autoComplete="off" name="com" />
              <label></label>
              <Field autoComplete="off" name="kh" />
              <label></label>
              <Field autoComplete="off" name="study" />
              <button type="submit" onClick={navigate("#")}>
                
              </button>
            </Form>
          </Formik>
        </S.InputContainer>
      </S.InputTimeContainer>
    </>
  );
};
